import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import "./styles.css";
import { Playground, store } from "graphql-playground-react";
import { ApolloLink } from "apollo-link";
import { createHttpLink } from "apollo-link-http";
import { uncrunch } from 'graphql-crunch';



const rootElement = document.getElementById("root");

const endpoints = [
  { name: 'MOODAH STAGING', endpoint: 'https://kjjpup3by7.execute-api.ap-southeast-1.amazonaws.com/staging/graphql-introspect?crunch=2' },
  { name: 'SAMPOERNA PROD', endpoint: 'https://9d7o2v25e5.execute-api.ap-southeast-1.amazonaws.com/prod/graphql-introspect' },
]

const App = () => {
  return (
    <React.StrictMode>
      <Provider store={store}>
        <Playground
          endpoint={endpoints[0].endpoint}
          createApolloLink={session => {
            const httpLink = createHttpLink({
              uri: session.endpoint,
              headers: {
                ...session.headers,
              },
              fetch
            });

            const uncrunchLink = new ApolloLink((operation, forward) => {
              return forward(operation).map(response => {
                response.data = uncrunch(response.data);
                return response;
              });
            });

            return {
              link: (session.endpoint || '').includes('crunch=') ? uncrunchLink.concat(httpLink) : httpLink
            };
          }}
        />
      </Provider>
      <div
        style={{
          position: 'absolute',
          bottom: 10,
          left: '50%',
          justifyContent: 'center',
        }}
      >
        <div>
          {endpoints.map((endpoint, index) => {
            return (
              <button
                key={index}
                onClick={() => {
                  copy(endpoint.endpoint)
                  alert(`${endpoint.endpoint} \n\nCopied to clipboard, paste into the endpoint`)
                }}
              >
                {endpoint.name}
              </button>
            )
          })}
        </div>
      </div>
    </React.StrictMode>
  )
}

function copy(text) {
  var input = document.createElement('textarea');
  input.innerHTML = text;
  document.body.appendChild(input);
  input.select();
  var result = document.execCommand('copy');
  document.body.removeChild(input);
  return result;
}

ReactDOM.render(
  <App />,
  rootElement,
)
